<template>
  <div class="login-div">
    <h1>注册</h1>
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="账户">
        <el-input v-model="form.username"></el-input>
      </el-form-item>
      <el-form-item label="密码">
        <el-input v-model="form.password"></el-input>
      </el-form-item>
      <el-form-item label="确认密码">
        <el-input v-model="form.repeat_password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button  @click="doRegister">注册</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    name: 'register',
    data () {
      return {
        form: {
          username: '',
          password: '',
          repeat_password: ''
        }
      }
    },
    methods: {
      doRegister () {
        this.$httpPost("register", this.form).then((response) => {
          if (response.code === 0) {
            this.$router.push({path:'login'})
          } else {
            this.$message({
              message: response.msg,
              center: true,
              type: "warning"
            });
          }
        }).catch(() => {
          this.$message({
            message: '请求服务器异常, 请稍后再试',
            center: true,
            type: "error"
          });
        })
      }
    }
  }
</script>
<style>
  html {
    background-color: #DBDBDB;
  }
  .login-div {
    width: 400px;
    margin: 10% auto;
    background-color: #fff;
  }
  .el-form {
    padding: 20px;
  }
  .login-div button{
    background-color: #3eaf7c;
    color: #fff;
  }
  h1{
    padding-top: 20px;
    text-align: center;
  }
</style>